import React, { useState, useEffect } from 'react';
import {
  Heading,
  NumberInput,
  NumberInputField,
  NumberInputStepper,
  NumberIncrementStepper,
  NumberDecrementStepper,
  Stack, VStack, HStack,
  FormControl,
  FormLabel,
  FormErrorMessage,
  FormHelperText,
  Input,
  Button,
  Text,
  Flex
} from '@chakra-ui/react';
import * as Yup from 'yup';
import { useFormik, Form, Formik } from 'formik';
function HouseappForm() {
  const [formData, setFormData] = useState({
    age: '',
    url: '',
    address: '',
    currentfloor: '', // 
    totalfloor: '',  // 
    area: ''
  });

  const handleSubmitbackend = async (e) => {

    try {
      const response = await fetch('http://localhost:8000/House/add_house/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(e),
      });
      const data = await response.json();
      console.log(data); // 
      // 
      setFormData({
        age: '',
        url: '',
        address: '',
        currentfloor: '',
        totalfloor: '',
        area: ''
      });
    } catch (error) {
      console.error('Error adding house:', error);
    }

  };

  // const handleChange = (e) => {
  //   setFormData({ ...formData, [e.target.name]: e.target.value });
  // };

  const formik = useFormik({
    // STEP 2-1
    initialValues: {
      currentfloor: "",
      totalfloor: "",
      area: "",
      age: "",
      url: "",
      address: ""
    },
    // STEP 2 Yup  validationSchema
    validationSchema: Yup.object({

      // currentfloor: Yup.string().min(0, ""),
      // totalfloor: Yup.string().min(0, ""),
      // area: Yup.string().min(0, ""),
      url: Yup.string().required(""),
      address: Yup.string().required(""),
    }),
    onSubmit: handleSubmitbackend

  });

  return (
    <HStack>
      <Heading as="h3">
        
      </Heading>

      <form onSubmit={formik.handleSubmit} style={{ backgroundColor: "#B8FEFF" }}>
        <FormControl isInvalid={!!formik.errors.currentfloor && formik.touched.currentfloor}>
          <FormLabel htmlFor="currentfloor"></FormLabel>
          <Input id="currentfloor" name="currentfloor" {...formik.getFieldProps("currentfloor")} placeholder='' />
          <FormErrorMessage>{formik.errors.currentfloor}</FormErrorMessage>
        </FormControl>

        <FormLabel htmlFor="totalfloor">  </FormLabel>
        <FormControl isInvalid={!!formik.errors.totalfloor && formik.touched.totalfloor}>
          <Input id="totalfloor" name="totalfloor" {...formik.getFieldProps("totalfloor")} placeholder='' />
          <FormErrorMessage>{formik.errors.totalfloor}</FormErrorMessage>
        </FormControl>

        <FormControl isInvalid={!!formik.errors.area && formik.touched.area}>
          <FormLabel htmlFor="area">:</FormLabel>
          <Input id="area" {...formik.getFieldProps("area")} placeholder=''></Input>
          <FormErrorMessage>{formik.errors.area}</FormErrorMessage>



        </FormControl>
        <FormControl isInvalid={!!formik.errors.age && formik.touched.age}>
          <FormLabel htmlFor="age" >:</FormLabel>
          <Input id="age" {...formik.getFieldProps("age")} placeholder=''></Input>
          <FormErrorMessage>{formik.errors.age}</FormErrorMessage>


        </FormControl>
        <FormControl isInvalid={!!formik.errors.url && formik.touched.url}>
          <FormLabel htmlFor="url">:</FormLabel>
          <Input id="url" {...formik.getFieldProps("url")} placeholder=''></Input>
          <FormErrorMessage>{formik.errors.url}</FormErrorMessage>



        </FormControl>
        <FormControl isInvalid={!!formik.errors.address && formik.touched.address}>
          <FormLabel htmlFor="address">:</FormLabel>
          <Input id="address" {...formik.getFieldProps("address")} placeholder=''></Input>
          <FormErrorMessage>{formik.errors.address}</FormErrorMessage>


        </FormControl>
        <Flex justifyContent="flex-end">

          <Button type="submit" colorScheme='teal' width="full" >
            
          </Button>
        </Flex>

      </form >
    </HStack >
  );
}


export default HouseappForm;
